<template>
  <div class="wrap" ref="wrap">
  	<div class="invite_main">
  		<div class="user_msg" v-if="userData">
	  		<img class="user_head" :src="userData.avatarUrl" alt="" >
  			<div class="msg_center">
  				<div class="msg_name textEllipsis">{{userData.nickname}}</div>
  				<div class="msg_people_num">已邀请<span>{{userData.total}}</span>人</div>
  			</div>
  			<div class="msg_right">
  				<div class="msg_money">
  					<span class="">{{userData.award}}</span>元
  				</div>
  				<p class="msg_text">预计赏金</p>
  			</div>
  		</div>

  		<div class="invite_cont">
  			<div class="cont_tit">
  				<!-- <div class="tit_bor"></div>
  				邀请记录
  				<div class="tit_bor"></div> -->

  				<img :src="cdnPath + 'invite_tit.png'"/>

  			</div>
  			<div class="invite_list" >
  				<template v-if="inviteList.length>0">
	  				<div class="invite_blo" v-for="item,index in inviteList" :key="index" >
				  		<img class="blo_head" :src="item.avatarUrl" alt="">
		  				<p class="blo_name textEllipsis">{{item.nickname}}</p>
		  				<div class="blo_time">{{item.inviteTime}}</div>
	  				</div>
  				</template>
  				
  				<div class="invite_none" v-else>
	  				<img :src="cdnPath + 'noInvite.png'"/>
  				</div>
  			</div>
  			<img class="loading" v-if="loading" :src="cdnPath + 'loadMore.gif'"/>
  		</div>
  	</div>
  </div>
</template>

<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import wechatMixin from '@/mixins/wechat'
import {getInviteUserListApi, getInviteListTestApi, getInviteUserListEnterpriseApi, getInviteListTestEnterpriseApi} from '@/store/api/activity'
import localstorage from '@/utils/localstorage'
let timer = null
@Component({
	components: {
  },
  mixins: [wechatMixin]
})
export default class rewardList extends Vue {
	cdnPath = `${process.env.VUE_APP_CDNPATH}/rewardList/`
	endTime = new Date('2019/3/8 20:34:00').getTime()
	curTime = new Date().getTime()
	inviteList = []
	userData = {}
	loading = false
	pageInfo = {
	  page: 1,
	  count: 20,
	  totalPage: 0,
	  total: 0
	}
  type = ''
	onBridgeReady() {
    WeixinJSBridge.call('hideOptionMenu');
  }

	mounted() {
		
		let user = localstorage.get('rewardCompanyUser')
		let type = this.$route.query.type ? this.$route.query.type === 'company' ? 'company' :  'personage' : 'personage' 
		this.type = type
		if(user.listId == this.$route.params.id){
			this.userData = user
			console.log(this.userData)
		}
		if(type === 'company') {
			this.getInviteUserListEnterprise()
		}else {
			this.getInviteUserList()
		}
		this.setScroll()

		if (typeof this.WeixinJSBridge == "undefined") {
      if (document.addEventListener) {
          document.addEventListener('WeixinJSBridgeReady',  this.onBridgeReady, false);
      } else if (document.attachEvent) {
          document.attachEvent('WeixinJSBridgeReady',  this.onBridgeReady);
          document.attachEvent('onWeixinJSBridgeReady',  this.onBridgeReady);
	      }
	  } else {
	      this.onBridgeReady();
	  }
	}

	toInviteList() {
		this.$router.push({name: 'inviteList'})
	}

	getInviteUserList(page) {
		console.log(page)
		let data = {
			page: page || this.pageInfo.page || 1,
			count: this.pageInfo.count,
			vkey: this.userData.vkey,
			list_id: this.$route.params.id
		}
		this.loading = true
		getInviteUserListApi(data).then(res => {
			if(data.page === 1){
				this.inviteList = res.data
			}else {
				this.inviteList = this.inviteList.concat(res.data)
			}
			this.loading = false
			this.pageInfo.totalPage = res.meta.total/data.count || 0
			this.pageInfo.page = data.page
		}).catch(e => {
			console.log(e)
			this.loading = false
			this.inviteList = []
		})
	}

	getInviteUserListEnterprise(page) {
		let data = {
			page: page || this.pageInfo.page || 1,
			count: this.pageInfo.count,
			vkey: this.userData.vkey,
			list_id: this.$route.params.id
		}
		this.loading = true
		getInviteUserListEnterpriseApi(data).then(res => {
			if(data.page === 1){
				this.inviteList = res.data
			}else {
				this.inviteList = this.inviteList.concat(res.data)
			}
			this.loading = false
			this.pageInfo.totalPage = res.meta.total/data.count || 0
			this.pageInfo.page = data.page
		}).catch(e => {
			console.log(e)
			this.loading = false
			this.inviteList = []
		})
	}


	//获取当前可视范围的高度 
  getClientHeight() {
    var clientHeight = 0;
    if(document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(document.body.clientHeight, document.documentElement.clientHeight);
    } else {
        clientHeight = Math.max(document.body.clientHeight, document.documentElement.clientHeight);
    }
    return clientHeight;
 	}

 	//获取文档完整的高度
  getScrollHeight(){
	　　var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
	　　if(document.body){
	　　　　bodyScrollHeight = document.body.scrollHeight;
	　　}
	　　if(document.documentElement){
	　　　　documentScrollHeight = document.documentElement.scrollHeight;
	　　}
	　　scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
	　　return scrollHeight;
	}

	setScroll() {
    let clientHeight = this.getClientHeight()
    let that = this

    window.onscroll = function () {
    		//let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight
	      let scrollT = document.documentElement.scrollTop || document.body.scrollTop; //滚动条的垂直偏移
	      let scrollHeight = that.getScrollHeight()
	      if (scrollT > scrollHeight - clientHeight -20) {
	          console.log("到底部了");
          // 翻页
        	if (!that.loading && that.pageInfo.page < that.pageInfo.totalPage) {
        		if(that.type === 'company') {
        			that.getInviteUserListEnterprise(that.pageInfo.page + 1)
        		}else {
        			that.getInviteUserList(that.pageInfo.page + 1)
        		}
        	}
	      }
	  }
  }

	openPop (type, index) {
		if (type === 'center') {
			this.centerPop = true
			this.bottomPop = false
			if (index) {
				this.positionIndex = index
			}
		} else {
			this.bottomPop = true
			this.centerPop = false
		}
	}
	colsePop() {
		this.centerPop = false
		this.bottomPop = false
	}
}
</script>
